// Variables
$bg-secondary: hsl(188, 15%, 12%);
$bg-secondary-highlight: hsl(188, 15%, 25%);
// $bg-color: hsl(180, 10%, 10%);

$bg-primary: hsl(188, 16%, 6%);
$bg-primary-highlight: hsl(188, 16%, 15%);

$text-color: hsl(188, 15%, 97%);
$text-inactive: hsla(188, 15%, 35%, .5);

$primary: hsl(188, 76%, 20%);
$primary-gradient: linear-gradient(0deg, rgba(12,79,89,1) 0%, rgba(15,99,112,1) 100%);
$primary-highlight: hsl(188, 75%, 40%);


$primary-load: hsla(188, 76%, 20%, .5);

$link-highlight: hsl(188, 100%, 70%);


::-webkit-scrollbar {
    width: auto;
}
    
::-webkit-scrollbar-track {
    background-color: $bg-primary; 
}

::-webkit-scrollbar-thumb {
    background-color: $primary;

    &:hover {
        background-color: $primary-highlight;
    }
}

// ::-webkit-scrollbar-button {
//     background-color: $bg-secondary;
//     height: 10px;

//     &:hover {
//         background-color: $primary-highlight;
//     }
// }
    

html {
    // font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    //   "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    //   sans-serif;
    font-family: "Exo 2", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    // font-size: 1.1em;
    line-height: 1.25;
    margin: 0;
    padding: 0;

    // Firefox scrollbar color
    scrollbar-color: $primary $bg-primary;
}

body {
    position: relative;
    margin: 0;
    padding: 0;
    background: $bg-primary;
    color: $text-color;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
}

#root {
    position: relative;
    width: 100%;
    height: 100vh;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
}

.page-container {
    position: relative;
    width: 100%;
    // height: 100vh;
    // overflow-y: auto;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.sidebar-wrapper {
    position: relative;
    width: 25rem;
    min-width: 25rem;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background: $bg-primary;
    // transition: min-width .1s;
}

.resizer {
    position: absolute;
    width: 1rem;
    height: 100%;
    right: 0;
    top: 0;
    background-color: $bg-secondary;
    cursor: col-resize;
    z-index: 400;
    border-left: 1px solid black;
    border-right: 1px solid black;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    font-size: 1.5rem;
    color: $text-inactive;

    &:hover {
        background-color: $bg-secondary-highlight;
    }
}

.sidebar {
    position: relative;
    top: 0;
    left: 0;
    background: $bg-secondary;
    margin: 0;
    padding: 0;
    min-width: 24rem;
    width: 24rem;
    height: 100vh;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    border-right: 1px solid black;
}


.will-header{
    position: relative;
    height: 12rem;
    background: $primary-gradient;
    text-align: center;
    padding: 3.2rem 0;
    box-sizing: border-box;
    border-bottom: 2px solid black;

    a {
        position: relative;
        color: $text-color;
        text-decoration: none;

        &:hover {
            color: $link-highlight;
        }
    }

    // a[title]:hover:after {
    //     content: attr(title);
    //     position: absolute;
    //     top: -4em;
    //     right: 1em;
    //     // left: -75%;
    //     z-index: 100;
    //     // text-align: left;
    //     background: $bg-secondary;
    //     // width: 10em;
    //     padding: .5em;
    //     box-sizing: border-box;
    // }


    h1 {
        margin: 0 0 1rem 0;
        font-size: 2.25rem;
    }
    
    .portfolio-icon {
        font-size: 1.6rem;
        margin: 0 .5rem;
    }
}

.projects-list-wrapper {
    position: relative;
    height: calc(100vh - 12rem);
}

.projects-list {
    // Chrome scrollbar properties
    &::-webkit-scrollbar {
        width: 5px;
    }
    // Firefox scrollbar properties
    scrollbar-color: $primary $bg-primary;
    scrollbar-width: thin;
    
    position: relative;
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    overflow-y: scroll;
    box-sizing: border-box;
    direction: rtl;
    list-style: none;

    
}

.project {
    position: relative;
    margin: 0;
    // padding: 2rem;
    // height: 3rem;
    overflow: hidden;
    direction: ltr;
    text-align: left;
    cursor: pointer;
    transition: height .5s;
    // background-color: $primary;
    border-bottom: 2px solid black;
    width: 100%;
}

.project-link {
    position: relative;
    width: 100%;
    display: block;
    margin: 0;
    padding: 2rem 3rem 1rem 3rem;
    box-sizing: border-box;
    color: $text-color;
    text-decoration: none;

    h2 {
        font-size: 1.4rem;
    }

    &:hover {
        background-color: $primary-highlight;
    }
}

.project-link-active {
    background-color: $primary;
}

.project-links-external {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    // margin-top: -4.5rem;
    // background-color: $bg-primary-highlight;
}

.project-links-external-link {
    position: relative;
    width: 100%;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    // height: 3.5em;
    padding: 1.5rem;
    box-sizing: border-box;
    color: $text-color;
    font-size: 1.4rem;
    flex-grow: 1;

    &:hover {
        background-color: $primary-highlight;
    }
}


.viewport {
    position: relative;
    width: 100%;
    height: 100vh;
    // overflow: auto;
}

.welcome {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    // overflow: hidden;
    // background-image: linear-gradient(60deg, $bg-primary 0%, hsla(188, 50%, 20%, .1) 100%), repeating-radial-gradient(circle at left, hsl(188, 20%, 5px) 0%, hsl(188, 50%, 15%) 100px);
    background-image: linear-gradient(120deg, rgba(12,17,17,1) 0%, rgba(12,79,89,.5) 100%);
}

.no-match-box {
    position: relative;
    background-color: $bg-secondary;
    max-width: 30rem;
    box-shadow: .5rem .5rem 1rem rgba(0, 0, 0, 0.3);
    padding: 3rem;
    box-sizing: border-box;

    h2 {
        margin: 0;
    }
}

.resizer-pulser-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
}

.resizer-pulser-container {
    position: relative;
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    left: -3rem;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    pointer-events: none;

}

.resizer-pulser {
    width: 0;
    height: 0;
    border-radius: 50%;
    // background: $primary-highlight;
    box-shadow: 0 0 1rem .5rem $primary-highlight inset;

    animation-name: pulser;
    animation-duration: 5s;
    animation-iteration-count: 5;
    // animation-timing-function: linear;
}

@keyframes pulser {
    0% {
        width: 0;
        height: 0;
        opacity: 1;
    }
    25% {
        width: 5rem;
        height: 5rem;
        opacity: 0;
    }
    26% {
        width: 0;
        height: 0;
    }
    75% {
        width: 0;
        height: 0;
        opacity: 1;
    }
    100% {
        width: 5rem;
        height: 5rem;
        opacity: 0;
    }
}

.resizer-mask {
    position: fixed;
    width: 100vw;
    height: 100vh;
    box-sizing: border-box;
    z-index: 300;
    pointer-events: none;
}

.resizer-mask-active {
    pointer-events: all;
}

.heroku-loading,
.heroku-notification {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}

.heroku-loading {
    overflow: hidden;
}


.heroku-loading-circle {
    position: relative;
    border-radius: 50%;
    border-top: 1rem solid $primary-load;
    border-right: 1rem double $primary-load;
    border-bottom: 1rem dashed $primary-load;
    border-left: 1rem solid transparent;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;

    animation-name: load-spin;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}


.outer-circle {
    min-width: 25rem;
    height: 25rem;

    animation-duration: 5s;
}

.inner-circle {
    min-width: 18rem;
    height: 18rem;
    
    animation-duration: 4s;
    animation-direction: reverse;
}

.inner-inner-circle {
    min-width: 12rem;
    height: 12rem;
    
    animation-duration: 3s;
}

@keyframes load-spin {
    0%  {transform: rotate(0deg);}
    100%    {transform: rotate(360deg);}
}

.heroku-notification {
    padding: 2rem;
    text-align: center;
    font-size: 1.4em;
}


iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100% - 4px);
    border: none;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: transparent;
}







@media (max-width: 900px) {
    .viewport {
      display: none;
    }

    // .page-container {
    //     flex-direction: column;
    //     width: 100%;
    //     box-sizing: border-box;
    // }

    .sidebar-wrapper {
        width: 100%;
        min-width: 100%;
        box-sizing: border-box;
    }

    .resizer {
        display: none;
    }

    .sidebar {
        position: relative;
        width: 100%;
        min-width: 100%;
        // height: auto;
        box-sizing: border-box;
        overflow: auto;
    }

    .will-header {
        padding: 1rem;
        height: auto;
        box-sizing: border-box;
    }

    .projects-list-wrapper {
        height: auto;
        // width: 100%;
        // overflow-x: scroll;
        // overflow-y: hidden;
        // box-sizing: border-box;
    }

    .projects-list {
        // width: 780%;
        // direction: ltr;
        // box-sizing: border-box;
        // overflow-y: hidden;
        overflow: visible;
    }

    // .project {
    //     float: left;
    //     width: 80vw;
    //     height: 16rem;
    //     box-sizing: border-box;
    //     border-right: solid 2px black;
    // }

    .project-link {
        // height: 11.5rem;
        // overflow-y: auto;
        box-sizing: border-box;
        background-color: transparent !important;
    }

    .project-link-active {
    }




    .welcome {
        position: relative;
        box-sizing: border-box;

    }

    .resizer-pulser-wrapper {
        display: none;
    }
}


@media (max-width: 500px) {
    .project-link {
        padding: 1rem;
    }
}