  background-image: -webkit-linear-gradient(330deg, #0c1111 0%, rgba(12, 79, 89, 0.5) 100%);
  background-image: linear-gradient(120deg, #0c1111 0%, rgba(12, 79, 89, 0.5) 100%); }

.no-match-box {
