.resizer-pulser-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  box-sizing: border-box; }

.resizer-pulser-container {
  position: relative;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  left: -3rem;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  pointer-events: none; }

.resizer-pulser {
  width: 0;
  height: 0;
  border-radius: 50%;
  box-shadow: 0 0 1rem 0.5rem #1a9eb3 inset;
  -webkit-animation-name: pulser;
          animation-name: pulser;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-iteration-count: 5;
          animation-iteration-count: 5; }

@-webkit-keyframes pulser {
  0% {
    width: 0;
    height: 0;
    opacity: 1; }
  25% {
    width: 5rem;
    height: 5rem;
    opacity: 0; }
  26% {
    width: 0;
    height: 0; }
  75% {
    width: 0;
    height: 0;
    opacity: 1; }
  100% {
    width: 5rem;
    height: 5rem;
    opacity: 0; } }

@keyframes pulser {
  0% {
    width: 0;
    height: 0;
    opacity: 1; }
  25% {
    width: 5rem;
    height: 5rem;
    opacity: 0; }
  26% {
    width: 0;
    height: 0; }
  75% {
    width: 0;
    height: 0;
    opacity: 1; }
  100% {
    width: 5rem;
    height: 5rem;
    opacity: 0; } }

.resizer-mask {
  position: fixed;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  z-index: 300;
  pointer-events: none; }

.resizer-mask-active {
  pointer-events: all; }

.heroku-loading,
.heroku-notification {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box; }

.heroku-loading {
  overflow: hidden; }

.heroku-loading-circle {
  position: relative;
  border-radius: 50%;
  border-top: 1rem solid rgba(12, 79, 90, 0.5);
  border-right: 1rem double rgba(12, 79, 90, 0.5);
  border-bottom: 1rem dashed rgba(12, 79, 90, 0.5);
  border-left: 1rem solid transparent;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-animation-name: load-spin;
          animation-name: load-spin;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; }

.outer-circle {
  min-width: 25rem;
  height: 25rem;
  -webkit-animation-duration: 5s;
          animation-duration: 5s; }

.inner-circle {
  min-width: 18rem;
  height: 18rem;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  animation-direction: reverse; }

.inner-inner-circle {
  min-width: 12rem;
  height: 12rem;
  -webkit-animation-duration: 3s;
          animation-duration: 3s; }

@-webkit-keyframes load-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes load-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.heroku-notification {
  padding: 2rem;
  text-align: center;
  font-size: 1.4em; }

iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: calc(100% - 4px);
  border: none;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: transparent; }

@media (max-width: 900px) {
