    display: none; }
  .sidebar-wrapper {
    width: 100%;
    min-width: 100%;
    box-sizing: border-box; }
  .resizer {
    display: none; }
  .sidebar {
    position: relative;
    width: 100%;
    min-width: 100%;
    box-sizing: border-box;
    overflow: auto; }
  .will-header {
    padding: 1rem;
